@import url(https://fonts.googleapis.com/css2?family=Inter:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.dropdown {
    position: relative;
      top: 50px;
    margin: 0 auto;
    width: 220px;
    height: 20px;
    border: 1px solid #666;
   background: #475154;
}
.form{
    width: 50vh;
    height: 80vh;
    display: flex;
    margin-left: 30rem;
    flex-direction: column;
    justify-content: center;
}
.form > h1 {
    margin-bottom: 30px;
  }
  
  .form > input {
    padding: 20px;
    border-radius: 3px;
    margin-bottom: 20px;
    border: 1px solid lightgray;
    background: #fff;
    font-size: 16px;
    color: rgb(0, 0, 32);
    outline: none;
  }
  
  .form > input:focus {
    border: 1px solid rgb(0, 0, 196);
  }
  
  .form > label {
    padding-bottom: 10px;
    color: rgb(0, 0, 32);
    font-weight: bold;
  }
  
  .form > button {
    padding: 10px;
    border: none;
    background-color: rgb(110, 2, 87);
    font-weight: 500;
    font-size: 20px;
    border-radius: 3px;
    color: #fff;
    cursor: pointer;
    transition: 0.2s ease-in-out;
    margin-top: 10px;
  }
.text{
    padding:20%;
    color:yellowgreen;
    font-weight: 900;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-color: rgb(248, 246, 253);
}

body,
input,
button {
  font-family: "Inter", sans-serif;
}
.App{
  text-align:center;
}
nav{
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 10vh;
  background-color: rgb(73,79,82);
  color: white;
}
.nav-links{
  width: 40%;
  display: flex;
  color: white;
  justify-content: space-around;
  align-items: center;
  list-style: none;
}
.nav-links  > li{
   color :white;
} 
